"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'

interface ApplicationStats {
  total: number
  successful: number
  failed: number
  pending: number
}

export default function Dashboard() {
  const [stats, setStats] = useState<ApplicationStats>({
    total: 0,
    successful: 0,
    failed: 0,
    pending: 0
  })

  const [recentApplications, setRecentApplications] = useState<any[]>([])

  useEffect(() => {
    // Cargar estadísticas desde localStorage
    const savedStats = localStorage.getItem('applicationStats')
    if (savedStats) {
      setStats(JSON.parse(savedStats))
    }

    const savedApplications = localStorage.getItem('recentApplications')
    if (savedApplications) {
      setRecentApplications(JSON.parse(savedApplications))
    }
  }, [])

  const successRate = stats.total > 0 ? (stats.successful / stats.total) * 100 : 0

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">
          Dashboard de Aplicaciones
        </h1>
        <p className="text-xl text-gray-600 max-w-2xl mx-auto">
          Gestiona y monitorea el envío automático de tu CV a múltiples ofertas de trabajo
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Enviadas</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.total}</div>
            <p className="text-xs text-gray-500">aplicaciones totales</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Exitosas</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.successful}</div>
            <p className="text-xs text-gray-500">enviadas correctamente</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Fallidas</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{stats.failed}</div>
            <p className="text-xs text-gray-500">con errores</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Tasa de Éxito</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{successRate.toFixed(1)}%</div>
            <p className="text-xs text-gray-500">de aplicaciones exitosas</p>
          </CardContent>
        </Card>
      </div>

      {/* Progress Section */}
      {stats.total > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Progreso General</CardTitle>
            <CardDescription>
              Estado actual de tus aplicaciones de trabajo
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <div className="flex justify-between text-sm mb-2">
                <span>Aplicaciones Exitosas</span>
                <span>{stats.successful}/{stats.total}</span>
              </div>
              <Progress value={successRate} className="h-2" />
            </div>
          </CardContent>
        </Card>
      )}

      {/* Quick Actions */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Subir Nuevo CV</CardTitle>
            <CardDescription>
              Carga tu CV actualizado y comienza el proceso de envío automático
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Button 
              className="w-full" 
              onClick={() => window.location.href = '/upload'}
            >
              Subir CV y Enviar a Trabajos
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Configuración</CardTitle>
            <CardDescription>
              Personaliza tus preferencias de búsqueda de trabajo
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Button 
              variant="outline" 
              className="w-full"
              onClick={() => window.location.href = '/config'}
            >
              Configurar Preferencias
            </Button>
          </CardContent>
        </Card>
      </div>

      {/* Recent Applications */}
      {recentApplications.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Aplicaciones Recientes</CardTitle>
            <CardDescription>
              Últimas aplicaciones enviadas
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentApplications.slice(0, 5).map((app, index) => (
                <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div>
                    <p className="font-medium">{app.jobBoard}</p>
                    <p className="text-sm text-gray-500">{app.timestamp}</p>
                  </div>
                  <div className={`px-2 py-1 rounded-full text-xs font-medium ${
                    app.status === 'success' 
                      ? 'bg-green-100 text-green-800' 
                      : 'bg-red-100 text-red-800'
                  }`}>
                    {app.status === 'success' ? 'Exitosa' : 'Fallida'}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Empty State */}
      {stats.total === 0 && (
        <Card className="text-center py-12">
          <CardContent>
            <div className="max-w-md mx-auto">
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                ¡Comienza a enviar tu CV!
              </h3>
              <p className="text-gray-500 mb-6">
                Aún no has enviado ninguna aplicación. Sube tu CV para comenzar el proceso automático.
              </p>
              <Button 
                size="lg"
                onClick={() => window.location.href = '/upload'}
              >
                Subir Mi CV
              </Button>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
